import React, { useState } from 'react'
import SingleColor from './SingleColor'
import A from './Just'
import Values from 'values.js'
import {SketchPicker,SliderPicker,CirclePicker} from 'react-color'

function App() {
  const [color, setColor] = useState('')
  const [error, setError] = useState(false)
  const [list, setList] = useState(new Values('#f15025').all(5))

  const handleSubmit = (e) => {
    e.preventDefault()
    try {
      let colors = new Values(color).all(5)
      setList(colors)
    } catch (error) {
      setError(true)
      console.log(error)
    }
  }

  return (
    <>
      <section className='just2' >
        <h2 className="just2">color generator</h2>
      </section >

        
  <section className="just2"> 
        <SliderPicker className="just2"
        color={ color }
        onChangeComplete={ (color) => {setColor(color.hex)}}
      />
      </section>


      <h3 className="just2">you picked: {color}</h3>


        <section className="just2">
        <form onSubmit={handleSubmit}>
          <input
            type='text'
            value={color}
            onChange={(e) => setColor(e.target.value)}
            placeholder='#f15025'
            className={`${error ? 'error' : null}`}
          />
          <button className='btn' type='submit'>
            submit
          </button>
        </form>
      </section>
        

      
      <section className='colors '>
        {list.map((color, index) => {
          return (
            <SingleColor
              key={index}
              {...color}
              index={index}
              hexColor={color.hex}
            />
          )
        })}
      </section>
      <footer className="footer">

      </footer>
    </>
  )
}

export default App
